@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
:root{--color-background: #f2f8f8;--color-text-main: #FFFFFF;--color-text-secondary: #0B3954;--color-text-third: #000000;--color-text-error: #C81D25;--color-card-background-feature: #BFD7EA;--color-card-background-bottom: #1F1F1F;--color-card-background-top: #0B3954;--color-card-background-other: #4B4B4B;--color-button-background: #087E8B;--color-button-background-active: #0aa0b1;--color-button-outline: #FFFFFF;--color-button-text: #FFFFFF;--color-date-card-background: #FFFFFF;--color-date-card-text: #FF5A5F;--color-date-card-border: #1F1F1F}*{box-sizing:border-box}html{font-family:'Noto Sans JP', sans-serif}body{margin:0;background-color:#f2f8f8;background-color:var(--color-background)}

.expense-date{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:5.5rem;height:5.5rem;border:1px solid #ececec;background-color:var(--color-date-card-background);color:var(--color-text-secondary);border-radius:12px;border-color:var(--color-date-card-border);-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.expense-date__month{font-size:0.75rem;font-weight:bold}.expense-date__year{font-size:0.75rem}.expense-date__day{font-size:1.5rem;font-weight:bold;color:var(--color-date-card-text)}

.card{border-radius:6px;box-shadow:0 1px 8px rgba(0,0,0,0.25)}

.expense-item{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:0.5rem;margin:1rem 0;background-color:#4b4b4b}.expense-item__description{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:1rem;gap:1rem;-webkit-align-items:flex-end;align-items:flex-end;-webkit-flex-flow:column-reverse;flex-flow:column-reverse;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-flex:1 1;flex:1 1}.expense-item h2{color:#3a3a3a;font-size:1rem;margin:0 1rem;color:white;-webkit-flex-grow:1;flex-grow:1}.expense-item__price{font-size:1rem;font-weight:bold;color:white;background-color:var(--color-button-background);border:1px solid white;padding:0.5rem;border-radius:6px;width:8rem;text-align:right}@media (min-width: 580px){.expense-item__description{-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-flex:1 1;flex:1 1}.expense-item__description h2{font-size:1.25rem}.expense-item__price{font-size:1.25rem;padding:0.5rem 1.5rem}}.transaction-btn{background:none;border:none;color:#1F1F1F;-webkit-flex-grow:1;flex-grow:1;text-align:right}.transaction-change-btn{background-color:var(--color-button-background);border:none;border-radius:5px;color:var(--color-text-main);-webkit-flex-grow:1;flex-grow:1;text-align:left;margin-left:2px}.expense-title-change-form{width:40%}.newTitleForm{color:var(--color-text-main);font-size:0.8rem}.newTitleForm input{border-radius:5px;border:1px solid #1F1F1F}

.new-expense__controls{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:1rem;gap:1rem;margin-bottom:1rem;text-align:left}.new-expense__control label{font-weight:bold;margin-bottom:0.5rem;display:block;color:var(--color-text-secondary)}.new-expense__control input{font:inherit;padding:0.5rem;border-radius:6px;border:1px solid #ccc;width:20rem;max-width:100%}input.invalid{border-color:var(--color-date-card-text)}.new-expense__actions{height:4rem;margin-top:9px}#exit-button{background:none;border:none;color:var(--color-text-secondary);padding:10px}

.new-expense{background-color:var(--color-card-background-feature);padding:1rem;margin:2rem auto;width:50rem;max-width:95%;border-radius:6px;text-align:center;box-shadow:0 1px 8px rgba(0,0,0,0.25)}

.filter-div{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-end;align-items:flex-end}.filter-title{height:45px;margin:0 1rem}.filter-title h4{color:var(--color-text-main);font-weight:lighter}.drop-down{margin:0 1rem}.drop-down select{border-radius:5px;font-size:1rem}

.chart-bar{height:100%;width:8px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.chart-bar__inner{height:100%;width:100%;border:1px solid var(--color-card-background-bottom);border-radius:12px;background-color:var(--color-background);overflow:hidden;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end}.chart-bar__fill{background-color:var(--color-card-background-other);width:100%;transition:all 0.3s ease-out}.chart-bar__label{color:var(--color-text-main);font-weight:bold;font-size:0.5rem;text-align:center}

.chart{padding:1rem;border-radius:12px;background-color:var(--color-card-background-top);text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;height:10rem;margin:2rem auto;width:50rem;max-width:95%}

.expenses{padding:1rem;background-color:var(--color-card-background-top);margin:2rem auto;width:50rem;max-width:95%}p.list-fallback{color:var(--color-text-main);text-align:center}

